import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const url = 'https://sarr.rshmelev.workers.dev'

const getMap = {
  method: 'get',
  url: url + '/map',
  headers: {
    'Content-Type': 'application/json'
  },
};

export default new Vuex.Store({
  state: {
      datas: {},
      communitiesInfo: {}
  },
  actions: {
    GET_Map : async ({commit}) => {
      await axios(getMap)
          .then(response => response.data)
          .then(res => {
            commit('getMap', res)
          })
          .catch(function (error) {
            console.log(error);
          });
    },
    GET_Gromad : async ({commit}, data) => {
      await axios({
          method: 'post',
          url: url + '/gromada?id=' + data,
          headers: {
            'Content-Type': 'application/json'
          }
      })
          .then(response => response.data)
          .then(res => {
            commit('getGromad', res)
          })
          .catch(function (error) {
            console.log(error);
          });
    },
  },
  mutations: {
    getMap: (state, res) => {
      state.datas = res.data.oblasti
    },
    getGromad: (state, res) => {
        const data = res.data
        state.communitiesInfo.push(data)
        return state.communitiesInfo.push(data)
    }
  },
  modules: {

  },
  getters: {
    getNameOblasti: state => {
      return state.datas
    },
    getDatasGromad: state =>  {
        return state.communitiesInfo
    }
  }
})
